    20%, 60% { -webkit-transform: rotate(60deg); transform: rotate(60deg); -webkit-transform-origin: top left; transform-origin: top left; -webkit-animation-timing-function: ease-in-out; animation-timing-function: ease-in-out; }  
    40% { -webkit-transform: rotate(20deg); transform: rotate(20deg); -webkit-transform-origin: top left; transform-origin: top left; -webkit-animation-timing-function: ease-in-out; animation-timing-function: ease-in-out; } 
    80% { -webkit-transform: rotate(20deg) translateY(0); transform: rotate(20deg) translateY(0); opacity: 1; -webkit-transform-origin: top left; transform-origin: top left; -webkit-animation-timing-function: ease-in-out; animation-timing-function: ease-in-out; } 
    100% { -webkit-transform:  translateY(calc(100vh - 120px)) rotate(20deg); transform:  translateY(calc(100vh - 120px)) rotate(20deg); -webkit-animation-timing-function: ease-in-out; animation-timing-function: ease-in-out; }
